import { useStaticQuery , graphql , Link } from "gatsby"
import React from "react"
import Image from "gatsby-image"
import css from '../styles/header.module.scss'
import Avatar from '../assets/avatar.svg'
import Portfolio from '../assets/portfolio.svg'
import Resume from '../assets/resume.svg'
import Contact from '../assets/contact.svg'

const Header = () => {
const data = useStaticQuery(graphql`
    query HeaderQuery {
      avatar:file(relativePath: { eq: "anton.jpg" }) {
      childImageSharp {
        fluid(maxWidth: 2000, quality: 100){
          ...GatsbyImageSharpFluid_tracedSVG
        }
      }
    }
    }
  `)
 
  return (
  <header>
      <Image className = {css.avatar} fluid = {data.avatar.childImageSharp.fluid}  />
  
       <Link  activeClassName = {css.activeLink} to = '/'>
         <Avatar className = {css.avatarImg} />
          ABOUT ME
       </Link>
       <Link  activeClassName = {css.activeLink} to = '/portfolio'>
       <Portfolio />
          PORTFOLIO
       </Link>
       <Link  activeClassName = {css.activeLink} to = '/resume'>
       <Resume />
          RESUME
       </Link>
       <Link  activeClassName = {css.activeLink} to = '/contacts'>
       <Contact />
          CONTACTS
       </Link>
  </header>
)}



export default Header


